<template>
  <div id="app">
    <div class="col-12 col-md-9 col-xl-6 my-5 mx-auto chatroom">
      <div class="row mx-auto chatroom-head">
        <div class="col-9 col-md-10 chatroom-head-left">
          <div class="fs-20">{{ $t("chat") }}</div>
          <b-icon
            class="cursor text-white"
            icon="translate"
            @click="changeLocale()"
          ></b-icon>
        </div>
        <div class="col-3 col-md-2 chatroom-head-right" v-if="user">
          <div class="fs-14">
            <span>
              {{ user.name }}
            </span>
            <b-icon
              class="cursor text-white"
              icon="pencil-square"
              @click="editName()"
            ></b-icon>
          </div>
        </div>
      </div>

      <div class="chatroom-content">
        <div
          class="my-2"
          :class="{ 'text-end': item.isMe }"
          v-for="(item, key) in messages"
          :key="key"
        >
          <div class="msg-name">
            {{ item.user.name }}
          </div>
          <div class="msg-text" :class="{ 'ms-auto': item.isMe }">
            <span>
              {{ item.text }}
            </span>
          </div>
        </div>
        <div ref="endOfContents"></div>
      </div>
      <div class="row mx-auto chatroom-input py-3" v-if="user">
        <textarea
          type="text"
          class="col"
          v-model="textInput"
          @keyup.enter="sendMsg()"
        />
        <div class="col-1">
          <b-icon class="h2" icon="telegram" @click="sendMsg()"></b-icon>
        </div>
      </div>
      <div class="row no-name mx-auto chatroom-input py-3" v-else>
        <input
          type="text"
          class="col"
          maxlength="4"
          :placeholder="$t('Please-enter-name')"
          v-model="nameInput"
          @keyup.enter="saveName()"
        />
        <div class="col-1">
          <b-icon class="h2" icon="telegram" @click="saveName()"></b-icon>
        </div>
      </div>
    </div>
  </div>
</template>

<style lang="scss">
