<script>
// Import the functions you need from the SDKs you need
import { initializeApp } from "firebase/app";
// TODO: Add SDKs for Firebase products that you want to use
// https://firebase.google.com/docs/web/setup#available-libraries

// Your web app's Firebase configuration
// For Firebase JS SDK v7.20.0 and later, measurementId is optional
const firebaseConfig = {
  apiKey: "AIzaSyA5czFtBozZqDepGhvHdu7MzE9bRd3i1KQ",
  authDomain: "chatroom-f9e57.firebaseapp.com",
  databaseURL:
    "https://chatroom-f9e57-default-rtdb.asia-southeast1.firebasedatabase.app",
  projectId: "chatroom-f9e57",
  storageBucket: "chatroom-f9e57.appspot.com",
  messagingSenderId: "586924057571",
  appId: "1:586924057571:web:53cebeb0c45c31952cf6d6",
  measurementId: "G-QMY7MM36C2",
};

// Initialize Firebase
initializeApp(firebaseConfig);

const db = getDatabase();

import {
  onChildAdded,
  query,
  getDatabase,
  ref,
  push,
  serverTimestamp,
} from "firebase/database";
export default {
  name: "App",
  components: {},
  data() {
    return {
      user: null,
      nameInput: null,
      textInput: null,
      messages: [],
    };
  },
  watch: {
    messages: function (newValue) {
      this.scrollToBottom();
      if (this.user) {
        let local = this.user
        newValue.forEach((e) => {
          e.isMe = e.user.name === local.name && e.user.time === local.time;
        });
      }
    },
  },
  created() {},
  mounted() {
    this.knock()
    this.checkName();
    this.getFirebase();
  },
  computed: {},
  methods: {
    getFirebase() {
      const queryCondition = query(ref(db, "messages"));
      onChildAdded(queryCondition, (snapshot) => {
        const data = snapshot.val();
        this.messages.push(data);
      });
    },
    sendMsg() {
      let msg = this.textInput.trim();
      if (msg) {
        push(ref(db, "messages/"), {
          user: this.user,
          text: msg,
          timestamp: serverTimestamp(),
        });
        this.textInput = null;
      }
    },
    checkName() {
      let data = JSON.parse(localStorage.getItem("user"));
      if (data) {
        this.user = data;
      }
    },
    async saveName() {
      if (this.nameInput) {
        let data = {
          name: this.nameInput.trim(),
          time: Date.now(),
        };
        localStorage.setItem("user", JSON.stringify(data));
        this.checkName();
        this.nameInput = null;
      } else {
        alert("不可為空白");
      }
    },
    editName() {
      this.user = null;
      localStorage.removeItem("user");

      this.messages.forEach((e) => {
        e.isMe = false;
      });
    },
    scrollToBottom() {
      setTimeout(() => {
        this.$refs.endOfContents.scrollIntoView();
      }, 100);
    },
    changeLocale() {
      this.$i18n.locale = this.$i18n.locale === "en" ? "zh-tw" : "en";
    },
    knock(){
      let  formData  =  new  FormData ( ) ;
      formData.append("where", "chatroom");
      let  url = "https://script.google.com/macros/s/AKfycbwosETKkW_yQKqgQzeCBJ8CEcuUByWHjf97XoH_oF3BO_qEEaT4j715VvMfR6te_A/exec"
      this.axios
        .post(url, formData)
        .catch(function (error) {
          console.log(error);
        });   
    }
  },
};
</script>
